@import "variables";

.App {
    height: 100vh;
    background: $white-1;
}

.login-page {
    &__main {
        height: 100%;
        padding-top: 200px;
        // background-image: linear-gradient(to right top, #a1a1b9, #8a89c2, #7471ca, #5c58cf, #423dd2, #542cb7, #5b199d, #5c0084, #550056, #410132, #270718, #000000);
        // background-image: linear-gradient(to right top, #000000, #270718, #410132, #550056, #5c0084, #5c0084, #5c0084, #5c0084, #550056, #410132, #270718, #000000);
        // background-image: linear-gradient(to right top, #0d00c4, #53008b, #56005c, #460036, #2f001c, #240213, #18010a, #000000, #000000, #000000, #000000, #000000);
        background-image: linear-gradient(to left bottom, #cc2ad4, #b556e5, #a071ef, #8e86f2, #8496f0, #74a5f6, #6ab3f9, #69bff8, #52cffe, #41dfff, #46eefa, #5ffbf1);
    }

    &__container-main {
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 5px;
        border: 1px solid $black;
        padding: 40px 0;
        width: 300px !important;
    }

    &__form {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }
}

.profile-page {
    &__header {
        display: flex;
        justify-content: flex-end;
        align-items: center;
        background-color: $lightBlue;
        height: 50px;
        margin-bottom: 20px;
        padding: 10px;
    }

    &__form {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        border-radius: 5px;
        padding: 10px;
    }
}

